body{
    background-image: url("/static/images/chelsea\ table.png");
    background-repeat: no-repeat;
    background-position: center;
    background-attachment: fixed;
    background-size:cover;
    margin: 0;
    padding: 0;
}

.nav_bar{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    position: relative;
    left: -5%;
}

.nav_bar img{
    position: relative;
    margin-left: 7%;
}

.nav_bar a{
    display: flex;
    flex-direction: row;
    margin-right: 5%;
    font-family: Din;
    text-decoration: none;
    color: black;
    transition: 0.3s ease-in;
}

.nav_bar a:hover{
    background-color: blueviolet;
    padding: 4px;
    border-radius: 4px;
}

.epl_table{
    margin-top: 5%;
}



.epl_table h1{
    font-family:Coco_Sharp;
    font-size: 55px;
    position: relative;
    margin-left: 7%;
}

.premier_logo{
    position: relative;
    margin-top: 3%;
    margin-left: 39%;
 }
 

table{
    position: relative;
    margin-left: 7%;
    border-collapse: collapse;
